body,html {
    font-family: Roboto, sans-serif;
    height: 100%;
    margin: 0;
}

.container {
    display: flex;
    flex-flow: column;
    height: 100%;
}

header {
    height: 48px;
    flex: 0 1 auto;
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.2) 0 5px 15px 0;
    display: flex;
}

.wrapper {
    width: 620px;
    margin: 0 auto;
    display: inline-flex;
    align-items: center;
}

header .logo {
    display: flex;
    text-transform: capitalize;
    font-size: 16px;
    font-weight: 700;
    color: rgb(73, 166, 233);
    height: 16px;
    letter-spacing: 4px;
}

header nav {
    margin-left: auto;
}

header nav ul {
    padding: 0;
    display: inline-flex;
    list-style: none;
}

main {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

main section {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

header nav li {
    color: rgb(6, 50, 81);
    font-size: 16px;
    line-height: 24px;
    margin-right: 16px;
    font-family: "Open Sans", sans-serif;
}

.color-name {
    background-color: hsl(209, 61%, 16%);
    color: white;
    padding: 10px;
    font-size: 30px;
}